*,
*::before,
*::after {
	box-sizing: border-box;
}

html,
body {
	position: relative;
}

body {
	min-height: 100vh;
	height: 100%;
	display: flex;
	flex-direction: column;
	overflow-x: hidden !important;
	font-style: normal;
	font-family: "Roboto", sans-serif;
}
ul,
ol,
li,
p,
h1,
h2,
h3,
h4,
h5,
h6 {
	margin: 0;
	padding: 0;
	list-style: none;
}
a {
	text-decoration: none;
}
* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}
:focus-visible {
	outline: none;
}
.container {
	padding: 0 93px;
	@media (max-width: $md1) {
		padding: 0 63px;
	}
	@media (max-width: $md2) {
		padding: 0 33px;
	}
	@media (max-width: $md3) {
		padding: 0 10px;
	}
}
// page animation
.js-scroll {
	opacity: 0;
	transition: opacity 0ms;
	&.scrolled {
		opacity: 1;
		&.fade-in {
			animation: fade-in 1s ease-in-out 0s;
		}
		&.slide-in-left {
			animation: slide-in-left 1s ease-in-out 0s;
			@media (max-width: 480px) {
				animation: slide-in-top 0.6s ease-in-out 0s;
			}
		}
		&.slide-in-top {
			animation: slide-in-top 1s ease-in-out 0s;
		}
		&.slide-in-bottom {
			animation: slide-in-bottom 1s ease-in-out 0s;
		}
		&.slide-in-right {
			animation: slide-in-right 1s ease-in-out 0s;
			@media (max-width: 480px) {
				animation: fade-in-top 0.6s ease-in-out 0s;
			}
		}
	}
}

@keyframes slide-in-left {
	0% {
		transform: translateX(-100px);
		opacity: 0;
	}
	100% {
		transform: translateX(0);
		opacity: 1;
	}
}
@keyframes slide-in-top {
	0% {
		transform: translateY(-100px);
		opacity: 0;
	}
	100% {
		transform: translateY(0);
		opacity: 1;
	}
}
@keyframes slide-in-bottom {
	0% {
		transform: translateY(100px);
		opacity: 0;
	}
	100% {
		transform: translateY(0);
		opacity: 1;
	}
}
@keyframes slide-in-right {
	0% {
		transform: translateX(100px);
		opacity: 0;
	}
	100% {
		transform: translateX(0);
		opacity: 1;
	}
}
@keyframes fade-in {
	0% {
		opacity: 0;
	}
	100% {
		opacity: 1;
	}
}
