.section {
   &__hero {
      background: url(..//img/hero-bg.webp) center no-repeat;
      background-size: cover;
      min-height: 100vh;
      height: 100%;
      display: flex;
      align-items: center;
      position: relative;
      @media (max-width: 992px){
         display: block;
      }
   }
}
.hero {
   display: flex;
   align-items: center;
   gap: 43px;
   @media (max-width: 992px){
      justify-content: center;
      flex-direction: column;
      text-align: center;
      width: 100%;
   }
   &__img {
      z-index: 2;
      @media (max-width: 480px){
         max-width: 170px;
         width: 100%;
      }
      img{
         width: 100%;
      }
   }
   &__content {
      z-index: 2;
      max-width: 778px;
      width: 100%;
      @media (max-width: 992px){
         display: flex;
         align-items: center;
         flex-direction: column;
      }
   }
   &__title {
      font-family: "Roboto";
      font-style: normal;
      font-weight: 700;
      font-size: 48px;
      line-height: 1.16;
      color: #ffffff;
      margin-bottom: 45px;
      @media (max-width: 1122px){
         font-size: 36px;
      }
      @media (max-width: 480px){
         font-size: 30px;
      }
   }
   &__text {
      font-family: "Roboto";
      font-style: normal;
      font-weight: 400;
      font-size: 24px;
      line-height: 1.16;
      color: rgba(38, 80, 233, 0.7);
      margin-bottom: 45px;
      max-width: 626px;
      width: 100%;
      @media (max-width: 1122px){
         font-size: 22px;
      }
      @media (max-width: 480px){
         font-size: 18px;
      }
   }
   &__btn {
      font-family: "Roboto";
      font-style: normal;
      font-weight: 700;
      font-size: 36px;
      line-height: 1.16;
      color: #3b69a7;
      padding: 24px 199px;
      background: #ffffff;
      box-shadow: 0px 15px 10px rgba(59, 105, 167, 0.15);
      border-radius: 10px;
      transition: all 0.3s ease;
      @media (max-width: 1122px){
         font-size: 28px;
      }
      @media (max-width: 768px){
         width: 100%;
         padding: 24px 0;
         text-align: center;
      }
      &:hover {
         transition: all 0.3s ease;
         background: #3b69a7;
         color: #ffffff;
      }
   }
   &__subtitle {
      margin-top: 31px;
      text-align: center;
      max-width: 635px;
      margin-bottom: 0;
      @media (max-width: 992px){
         margin-top: 7px;
      }
   }
}
.img{
   position: absolute;
   right: 0;
   top: 0;
   bottom: 0;
   z-index: 0;
   height: 100%;
   img{
      width: 100%;
      height: 100%;
      z-index: -1;
      object-fit: cover;
   }
}